import React, { Component } from 'react'
import './static/css/app.css'
import BigText from 'react-bigtext'
import Gallery from 'react-grid-gallery'

var requireContext = require.context("./static/images/insp", true, /^\.\/.*\.jpg$/)

var imageSrcs = requireContext.keys().map(requireContext)

var bigTextOptions = {
    height: 100
}

var images = imageSrcs.map( url => (
    {
        src: url,
        thumbnail: url,
    }
)) 
    
// [
//     {
//         src: "src/static/images/insp/2.jpg",
//         thumbnail: "src/static/images/insp/2.jpg",
//         thumbnailWidth: 320,
//         thumbnailHeight: 174,
//     },
// ]


var thumbnailStyle = () => {
    return {
        "margin": "2px",
        "userSelect": "none",
        "position": "relative",
        "float": "left",
        "background": "rgb(238, 238, 238)",
        "padding": "0px",
    }
}

class App extends Component {


    
    constructor(props) {
        super(props)

        this.state = {
            lightboxIsOpen: false,
            
        }
    }
    
    render() {

        console.log(imageSrcs)
        console.log(images)
        
        
        return (
            <div className="appMainDiv">
                
                <div className="pageContainer">
                      
                    <div className="appH2">
                        <BigText
                            options={bigTextOptions}
                        > 
                            Ben Litwin Photography  
                        </BigText>
                    </div>
                        
                    <Gallery
                      images={images}
                      enableImageSelection={false}
                      lightboxWidth="800px"
                      // thumbnailStyle={thumbnailStyle}
                    />
                    
                </div>
                
            </div>
        )
    }
}

export default App;



